"use client";

import { useState, useCallback } from "react";
import { useToast } from "@/shared/ui/use-toast";
import { TeamsService } from "../api/teams-service";
import { Team, CreateTeamDto, TeamMember } from "@/entities/team/model/types";
import { useAuthContext } from "@/app/providers/auth-provider";

interface UseTeamsState {
  teams: Team[];
  currentTeam: Team | null;
  teamMembers: TeamMember[];
  isLoading: boolean;
  error: string | null;
}

export function useTeams() {
  const [state, setState] = useState<UseTeamsState>({
    teams: [],
    currentTeam: null,
    teamMembers: [],
    isLoading: false,
    error: null,
  });
  const { toast } = useToast();
  const { token } = useAuthContext();

  const createTeam = useCallback(
    async (name: string) => {
      if (!token) {
        throw new Error("Не авторизован");
      }

      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));
        const team = await TeamsService.createTeam(name, token);
        setState((prev) => ({
          ...prev,
          teams: [...prev.teams, team],
          currentTeam: team,
          isLoading: false,
        }));

        toast({
          title: "Успешно!",
          description: "Команда создана",
        });

        return team;
      } catch (error) {
        const message =
          error instanceof Error
            ? error.message
            : "Ошибка при создании команды";
        setState((prev) => ({ ...prev, error: message, isLoading: false }));

        toast({
          variant: "destructive",
          title: "Ошибка!",
          description: message,
        });

        throw error;
      }
    },
    [toast, token]
  );

  const joinTeam = useCallback(
    async (code: string) => {
      if (!token) {
        throw new Error("Не авторизован");
      }

      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));
        await TeamsService.joinTeam({ team_code: code }, token);
        const team = await TeamsService.getTeamByCode(code, token);
        setState((prev) => ({
          ...prev,
          teams: [...prev.teams, team],
          currentTeam: team,
          isLoading: false,
        }));

        toast({
          title: "Успешно!",
          description: "Вы присоединились к команде",
        });

        return team;
      } catch (error) {
        const message =
          error instanceof Error
            ? error.message
            : "Ошибка при присоединении к команде";
        setState((prev) => ({ ...prev, error: message, isLoading: false }));

        toast({
          variant: "destructive",
          title: "Ошибка!",
          description: message,
        });

        throw error;
      }
    },
    [toast, token]
  );

  const fetchMyTeams = useCallback(async () => {
    if (!token) {
      throw new Error("Не авторизован");
    }

    try {
      setState((prev) => ({ ...prev, isLoading: true, error: null }));
      const teams = await TeamsService.getMyTeams(token);
      setState((prev) => ({ ...prev, teams, isLoading: false }));
      return teams;
    } catch (error) {
      const message =
        error instanceof Error
          ? error.message
          : "Ошибка при получении списка команд";
      setState((prev) => ({ ...prev, error: message, isLoading: false }));
      throw error;
    }
  }, [token]);

  const getTeamById = useCallback(
    async (teamId: number) => {
      if (!token) {
        throw new Error("Не авторизован");
      }

      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));
        const team = await TeamsService.getTeamById(teamId, token);
        setState((prev) => ({ ...prev, currentTeam: team, isLoading: false }));
        return team;
      } catch (error) {
        const message =
          error instanceof Error
            ? error.message
            : "Ошибка при получении команды";
        setState((prev) => ({ ...prev, error: message, isLoading: false }));
        throw error;
      }
    },
    [token]
  );

  const getTeamByCode = useCallback(
    async (code: string) => {
      if (!token) {
        throw new Error("Не авторизован");
      }

      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));
        const team = await TeamsService.getTeamByCode(code, token);
        setState((prev) => ({ ...prev, currentTeam: team, isLoading: false }));
        return team;
      } catch (error) {
        const message =
          error instanceof Error
            ? error.message
            : "Ошибка при получении команды";
        setState((prev) => ({ ...prev, error: message, isLoading: false }));
        throw error;
      }
    },
    [token]
  );

  const getTeamMembers = useCallback(
    async (teamId: number) => {
      if (!token) {
        throw new Error("Не авторизован");
      }

      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));
        const members = await TeamsService.getTeamMembers(teamId, token);
        setState((prev) => ({
          ...prev,
          teamMembers: members,
          isLoading: false,
        }));
        return members;
      } catch (error) {
        const message =
          error instanceof Error
            ? error.message
            : "Ошибка при получении участников команды";
        setState((prev) => ({ ...prev, error: message, isLoading: false }));
        throw error;
      }
    },
    [token]
  );

  const deleteTeam = useCallback(
    async (teamId: number) => {
      if (!token) {
        throw new Error("Не авторизован");
      }

      try {
        setState((prev) => ({ ...prev, isLoading: true, error: null }));
        await TeamsService.deleteTeam(teamId, token);
        setState((prev) => ({
          ...prev,
          teams: prev.teams.filter((team) => team.id !== teamId),
          currentTeam:
            prev.currentTeam?.id === teamId ? null : prev.currentTeam,
          isLoading: false,
        }));

        toast({
          title: "Успешно!",
          description: "Команда удалена",
        });
      } catch (error) {
        const message =
          error instanceof Error
            ? error.message
            : "Ошибка при удалении команды";
        setState((prev) => ({ ...prev, error: message, isLoading: false }));

        toast({
          variant: "destructive",
          title: "Ошибка!",
          description: message,
        });

        throw error;
      }
    },
    [toast, token]
  );

  return {
    ...state,
    createTeam,
    joinTeam,
    fetchMyTeams,
    getTeamById,
    getTeamByCode,
    getTeamMembers,
    deleteTeam,
  };
}
