"use client";

import { useEffect, useState, useMemo } from "react";
import { useTeams } from "@/features/teams/lib/use-teams";
import { Team, TeamMember } from "@/entities/team/model/types";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
  CardFooter,
} from "@/shared/ui/card";
import { Button } from "@/shared/ui/button";
import { Input } from "@/shared/ui/input";
import { Label } from "@/shared/ui/label";
import { useToast } from "@/shared/ui/use-toast";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/shared/ui/dialog";
import { User, Users, Plus, Copy, Trash2 } from "lucide-react";
import { useAuthContext } from "@/app/providers/auth-provider";
import { Avatar, AvatarFallback } from "@/shared/ui/avatar";

export default function TeamsPage() {
  const {
    isLoading,
    teams,
    createTeam,
    joinTeam,
    fetchMyTeams,
    getTeamMembers,
    deleteTeam,
  } = useTeams();
  const { toast } = useToast();
  const { user } = useAuthContext();
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [isJoinDialogOpen, setIsJoinDialogOpen] = useState(false);
  const [isDeleteDialogOpen, setIsDeleteDialogOpen] = useState(false);
  const [teamToDelete, setTeamToDelete] = useState<number | null>(null);
  const [newTeamName, setNewTeamName] = useState("");
  const [joinCode, setJoinCode] = useState("");
  const [teamMembers, setTeamMembers] = useState<{
    [key: number]: TeamMember[];
  }>({});
  const [selectedTeam, setSelectedTeam] = useState<number | null>(null);

  // Check if user already has a created team
  const hasCreatedTeam = useMemo(() => {
    if (!user?.id || !teams) return false;
    return teams.some((team) => team.creator_id === Number(user.id));
  }, [teams, user?.id]);

  useEffect(() => {
    fetchMyTeams().catch(console.error);
  }, [fetchMyTeams]);

  const handleCreateTeam = async (e: React.FormEvent) => {
    e.preventDefault();

    if (hasCreatedTeam) {
      toast({
        title: "Ошибка",
        description:
          "Вы уже создали команду. Можно создать только одну команду.",
        variant: "destructive",
      });
      return;
    }

    if (!newTeamName.trim()) {
      toast({
        title: "Ошибка",
        description: "Введите название команды",
        variant: "destructive",
      });
      return;
    }

    try {
      await createTeam(newTeamName);
      await fetchMyTeams();
      setIsCreateDialogOpen(false);
      setNewTeamName("");
      toast({
        title: "Успешно",
        description: "Команда успешно создана",
      });
    } catch (error) {
      console.error("Error creating team:", error);
      toast({
        title: "Ошибка",
        description:
          error instanceof Error ? error.message : "Не удалось создать команду",
        variant: "destructive",
      });
    }
  };

  const handleJoinTeam = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await joinTeam(joinCode);
      setIsJoinDialogOpen(false);
      setJoinCode("");
      toast({
        title: "Успешно",
        description: "Вы успешно присоединились к команде",
      });
    } catch (error) {
      console.error("Error joining team:", error);
      toast({
        title: "Ошибка",
        description:
          error instanceof Error
            ? error.message
            : "Не удалось присоединиться к команде",
        variant: "destructive",
      });
    }
  };

  const copyTeamCode = (code: string) => {
    navigator.clipboard.writeText(code);
    toast({
      title: "Код скопирован",
      description: "Код команды скопирован в буфер обмена",
    });
  };

  const handleShowMembers = async (teamId: number) => {
    if (selectedTeam === teamId) {
      setSelectedTeam(null);
      return;
    }

    setSelectedTeam(teamId);
    if (!teamMembers[teamId]) {
      try {
        const members = await getTeamMembers(teamId);
        setTeamMembers((prev) => ({ ...prev, [teamId]: members }));
      } catch (error) {
        console.error("Error fetching team members:", error);
        toast({
          title: "Ошибка",
          description: "Не удалось загрузить участников команды",
          variant: "destructive",
        });
      }
    }
  };

  const handleDeleteTeam = async () => {
    if (!teamToDelete) return;

    try {
      await deleteTeam(teamToDelete);
      setIsDeleteDialogOpen(false);
      setTeamToDelete(null);
      toast({
        title: "Успешно",
        description: "Команда удалена",
      });
    } catch (error) {
      console.error("Error deleting team:", error);
      toast({
        title: "Ошибка",
        description:
          error instanceof Error ? error.message : "Не удалось удалить команду",
        variant: "destructive",
      });
    }
  };

  const openDeleteDialog = (teamId: number) => {
    setTeamToDelete(teamId);
    setIsDeleteDialogOpen(true);
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[60vh]">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl md:text-3xl font-bold tracking-tight">
            Команды
          </h1>
          <p className="text-sm md:text-base text-gray-600">
            Управление командами и участниками
          </p>
        </div>
        <div className="flex gap-2">
          <Dialog open={isJoinDialogOpen} onOpenChange={setIsJoinDialogOpen}>
            <DialogTrigger asChild>
              <Button variant="outline">Присоединиться</Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Присоединиться к команде</DialogTitle>
                <DialogDescription>
                  Введите код команды, чтобы присоединиться к ней
                </DialogDescription>
              </DialogHeader>
              <form onSubmit={handleJoinTeam}>
                <div className="space-y-4 py-4">
                  <div className="space-y-2">
                    <Label htmlFor="code">Код команды</Label>
                    <Input
                      id="code"
                      value={joinCode}
                      onChange={(e) => setJoinCode(e.target.value)}
                      placeholder="Введите код команды"
                    />
                  </div>
                </div>
                <DialogFooter>
                  <Button type="submit">Присоединиться</Button>
                </DialogFooter>
              </form>
            </DialogContent>
          </Dialog>

          <Dialog
            open={isCreateDialogOpen}
            onOpenChange={setIsCreateDialogOpen}
          >
            <DialogTrigger asChild>
              <Button
                disabled={hasCreatedTeam}
                title={hasCreatedTeam ? "Вы уже создали команду" : ""}
              >
                <Plus className="h-4 w-4 mr-2" />
                Создать команду
              </Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Создать новую команду</DialogTitle>
                <DialogDescription>
                  {hasCreatedTeam
                    ? "Вы уже создали команду. Можно создать только одну команду."
                    : "Создайте команду для совместной работы над проектом"}
                </DialogDescription>
              </DialogHeader>
              <form onSubmit={handleCreateTeam}>
                <div className="space-y-4 py-4">
                  <div className="space-y-2">
                    <Label htmlFor="name">Название команды</Label>
                    <Input
                      id="name"
                      value={newTeamName}
                      onChange={(e) => setNewTeamName(e.target.value)}
                      placeholder="Введите название команды"
                      disabled={hasCreatedTeam}
                    />
                  </div>
                </div>
                <DialogFooter>
                  <Button type="submit" disabled={hasCreatedTeam}>
                    Создать команду
                  </Button>
                </DialogFooter>
              </form>
            </DialogContent>
          </Dialog>
        </div>
      </div>

      {teams.length === 0 ? (
        <Card>
          <CardContent className="flex flex-col items-center justify-center py-10">
            <Users className="h-12 w-12 text-gray-400 mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">
              У вас пока нет команд
            </h3>
            <p className="text-sm text-gray-600 text-center max-w-sm mb-6">
              Создайте новую команду или присоединитесь к существующей,
              используя код приглашения
            </p>
            <div className="flex gap-4">
              <Button
                variant="outline"
                onClick={() => setIsJoinDialogOpen(true)}
              >
                Присоединиться к команде
              </Button>
              <Button onClick={() => setIsCreateDialogOpen(true)}>
                Создать команду
              </Button>
            </div>
          </CardContent>
        </Card>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {teams.map((team) => (
            <Card key={team.id} className="flex flex-col">
              <CardHeader>
                <div className="flex justify-between items-start">
                  <CardTitle>{team.name}</CardTitle>
                  {user?.id && team.creator_id === Number(user.id) && (
                    <Button
                      variant="ghost"
                      size="icon"
                      onClick={() => openDeleteDialog(team.id)}
                    >
                      <Trash2 className="h-4 w-4 text-red-500" />
                    </Button>
                  )}
                </div>
                <CardDescription>
                  Код команды:{" "}
                  <Button
                    variant="link"
                    className="px-0"
                    onClick={() => copyTeamCode(team.code)}
                  >
                    {team.code}
                    <Copy className="h-4 w-4 ml-1" />
                  </Button>
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="flex items-center gap-2">
                  <User className="h-4 w-4" />
                  <span className="text-sm text-gray-600">
                    {team.creator_id === Number(user?.id)
                      ? "Создатель"
                      : "Участник"}
                  </span>
                </div>
              </CardContent>
              <CardFooter className="flex flex-col items-stretch mt-auto">
                <Button
                  variant="outline"
                  className="w-full"
                  onClick={() => handleShowMembers(team.id)}
                >
                  {selectedTeam === team.id
                    ? "Скрыть участников"
                    : "Показать участников"}
                </Button>
                {selectedTeam === team.id && teamMembers[team.id] && (
                  <div className="mt-4 space-y-2">
                    {teamMembers[team.id].map((member) => (
                      <div
                        key={member.id}
                        className="flex items-center gap-2 p-2 rounded-lg bg-gray-50"
                      >
                        <Avatar className="h-8 w-8">
                          <AvatarFallback>
                            {member.fullname
                              .split(" ")
                              .map((n) => n[0])
                              .join("")
                              .toUpperCase()}
                          </AvatarFallback>
                        </Avatar>
                        <div className="flex-1 min-w-0">
                          <p className="text-sm font-medium truncate">
                            {member.fullname}
                          </p>
                          <p className="text-xs text-gray-500 truncate">
                            {member.email}
                          </p>
                        </div>
                        {team.creator_id === Number(member.id) && (
                          <span className="text-xs text-gray-500">
                            Создатель
                          </span>
                        )}
                      </div>
                    ))}
                  </div>
                )}
              </CardFooter>
            </Card>
          ))}
        </div>
      )}

      {/* Delete Confirmation Dialog */}
      {isDeleteDialogOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 max-w-md w-full mx-4">
            <h3 className="text-lg font-semibold mb-2">Удалить команду?</h3>
            <p className="text-gray-600 mb-6">
              Это действие нельзя отменить. Команда будет удалена безвозвратно.
            </p>
            <div className="flex justify-end gap-4">
              <Button
                variant="outline"
                onClick={() => {
                  setIsDeleteDialogOpen(false);
                  setTeamToDelete(null);
                }}
              >
                Отмена
              </Button>
              <Button variant="destructive" onClick={handleDeleteTeam}>
                Удалить
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
